import React from "react";

const Socials = ({ socials }) => {
    return (
        <>
            <div id="social-icons" className="social-icons">
                {socials.map(social => {
                    return <a key={social.key} className="social-icon" href={social.url} target="_blank" rel="noopener noreferrer"><i
                    className={social.favicon}></i></a>
                })}
            </div>
        </>
    )
}

export default Socials;