import React from "react";

const Project = ({ project }) => {
    return (
    <div id={project.title} className="project-card">
        <div className="project-image-container">
            <img className="project-image" src={project.img} alt={project.title} />
        </div>
        <div className="project-card-details">
            <div className="project-title">{project.title}</div>
            <div className="skills">
                {project.skills.map(skill => {
                    return <div key={skill} className="skill">{skill}</div>
                })}
            </div>
            <div className="project-details">
                <p><span className="small-text medium-text">{project.description}</span></p>
            </div>
            <div className="project-buttons">
                {project.buttons.map(button => {
                    return <a key={button.text} className="project-button" href={button.url} target="_blank"
                        rel="noopener noreferrer"><i className={button.favicon}></i>{button.text}</a>
                })}
            </div>
        </div>
    </div>)
}

export default Project;