import React, { useState } from "react";
import About from "./components/About";
import Projects from "./components/Projects";
import Footer from "./components/Footer";

function App() {
  const [revealProjects, setRevealProjects] = useState(false)
  const socials = [
      new social("GitHub","https://github.com/stuartkenward","fab fa-github"), 
      new social("LinkedIn","https://www.linkedin.com/in/stuartkenward/","fab fa-linkedin-in"), 
      new social("Email","mailto:stukenward@gmail.com","fas fa-at")
    ]

  function social(key,url, favicon){
    this.key = key;
    this.url = url;
    this.favicon = favicon;
  }

  if (revealProjects) {
    return (
      <>
        <Projects socials={socials}/>
        <Footer />
      </>
    )
  }
  else {
    return (
      <>
        <About setRevealProjects={setRevealProjects} socials={socials}/>
      </>
    )
  }
}

export default App;
