import React, {useEffect} from "react";
import Socials from "./Socials";

const About = ({setRevealProjects, socials}) => {
    
    function typeWriter(text, line, i, fnCallback) {
        if (i < (text.length)) {
            if (line === 0 &&  document.getElementById("title")) {
                document.getElementById("title").innerHTML = text.substring(0, i + 1);
            }
            else if (line === 1 &&  document.getElementById("title")) {
                document.getElementById("subtitle").innerHTML = text.substring(0, i + 1);
            }
            setTimeout(function () {
                typeWriter(text, line, i + 1, fnCallback)
            }, 100);
        }
        else if (typeof fnCallback == 'function') {
            setTimeout(fnCallback, 200);
        }
    }

    function StartTextAnimation(line) {
        var dataText = ["Hi, i'm Stu.", "I'm a Software Engineer, based in Bournemouth."];
        if (line < dataText.length) {
            typeWriter(dataText[line], line, 0, function () {
                StartTextAnimation(line + 1);
            });
        }
    }

    useEffect(() => {
        StartTextAnimation(0);
    });

    return (
        <div id="about" className="about-content">
            <div className="animation">
                <div className="about">
                    <div id="title" className="title"></div>
                    <div id="subtitle" className="subtitle"></div>
                </div>
                <div id="view-projects-button-container" className="view-projects-button-container">
                    <div className="view-projects-button"  onClick={function(e) {setRevealProjects(true)}}>View Projects</div>
                </div>

                <Socials socials={socials}/>
            </div>
        </div>)
}

export default About;