import React, { useEffect, useState } from "react";
import Skill from "./Skill";
import Project from "./Project";
import Socials from "./Socials";
import weather from "../images/weather.png";
import coup from "../images/coup.png";
import portfolio from "../images/portfolio.png";
import githubjobs from "../images/github-jobs.png";

const Projects = ({socials}) => {
    const skills = ["Show All", "Python", "Css3", "Html5", "JavaScript", "React.js"]
    const projects = [
        new project(
            "Weather",
            weather,
            ["Css3", "Html5", "React.js", "JavaScript"],
            "Weather app that uses OpenWeather API and Google Geolocation API to get the weather data of the devices current location.",
            [new projectButton(
                "https://github.com/stuartkenward/weather", "fab fa-github", "View Code"
            ),
            new projectButton(
                "https://stuartkenward.github.io/weather/", "fas fa-eye", "View Website"
            )]),
        new project(
            "Portfolio",
            portfolio,
            ["Css3","Html5","React.js","JavaScript"],
            "Showcasing personal development projects using React",
            [new projectButton(
                "https://github.com/stuartkenward/portfolio-v2", "fab fa-github", "View Code"
            )]),
        new project(
            "GitHub Jobs",
            githubjobs,
            ["Css3", "Html5", "React.js", "JavaScript"],
            "Using the GitHub Jobs API to search for available jobs.",
            [new projectButton(
                "https://github.com/stuartkenward/github-jobs", "fab fa-github", "View Code"
            )]),
        new project(
            "Coup",
            coup,
            ["Python"],
            "Recreating the card game Coup in Python with basic AI using Pygame.",
            [new projectButton(
                "https://github.com/stuartkenward/Coup", "fab fa-github", "View Code"
            )])
        ]
    const [visableProjects, setVisableProjects] = useState(projects)
        
    function project(title, img, skills, description, buttons) {
        this.title = title;
        this.img = img;
        this.skills = skills;
        this.description = description;
        this.buttons = buttons;
    }

    function projectButton(url, favicon, text) {
        this.url = url;
        this.favicon = favicon;
        this.text = text;
    }

    function filter(skill) {
        check(skill.skill);
        for (let i = 0; i < skills.length; i++) {
            if (skills[i] !== skill.skill) {
                uncheck(skills[i]);
            }
        }
        document.getElementById(skill.skill).classList.toggle("transform-active");

        let projectList = [];
        if (skill.skill === "Show All") {
            for (let j = 0; j < projects.length; j++) {
                projectList.push(projects[j]);
            }
        }
        else {
            for (let k = 0; k < projects.length; k++) {
                let project = projects[k];
                if (project.skills.includes(skill.skill)) {
                    projectList.push(project);
                }
            }
        }
        setVisableProjects(projectList)
    }

    function check(id) {
        document.getElementById(id).checked = true;
    }

    function uncheck(id) {
        document.getElementById(id).checked = false;
    }

    useEffect(() => {
        check("Show All");
    }, []);

    return (
        <div id="projects-content" className="projects-content">
            <div className="animation">
                <div id="projects-about">
                    <div id="projects-title" className="title">Portfolio</div>
                </div>
                <Socials socials={socials}/>
            </div>
            <div id="filter-skills" className="filter">
                {skills.map(skill => {
                    return <Skill key={skill} skill={skill} filter={filter} />
                })}
            </div>

            <div className="projects transform">
                {visableProjects.map(project => {
                    return <Project key={project.title} project={project} />
                })}
            </div>
        </div>)
}

export default Projects;